import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';

@Component({
  selector: 'app-picture-box',
  templateUrl: './picture-box.component.html',
  styleUrls: ['./picture-box.component.css']
})
export class PictureBoxComponent implements OnInit {
  @Input() isUpload: false;
  @Input() images: string[];
  @Input() image: any;
  @Input() title: String;
  @Output() onSelect = new EventEmitter<string>();
  unknownURL = 'data:image/png;base64,';
  idx = 0;
  @ViewChild('fileInput') fileInput :ElementRef;
  file: string = '';
  item: {
    image: any
  } = {
    image: false
  }

  constructor() { }

  ngOnInit(): void {
    // console.log(this.images)
    // console.log(this.image)
  }

  clear () {
    this.item.image = false;
  }

  next () {
    this.idx++;
    if(this.idx >= this.images.length)
      this.idx = 0;
  }

  prev () {
    this.idx--;
    if(this.idx < 0)
      this.idx = this.images.length - 1;
  }

  selectImage() {
    this.fileInput.nativeElement.click();
  }

  handleFileUpload(event) {
    // console.log(event.target.files[0]);
    // console.log(this.$refs.file.files[0]);
    this.file = this.fileInput.nativeElement.files[0]
    this.createImage(this.file);
  }

  createImage(file) {
    var image = new Image();
    var reader = new FileReader();

    reader.onload = (e) => {
      this.item.image = e.target.result;
      this.onSelect.emit(this.item.image);
    };
    reader.readAsDataURL(file);
  }
}
